<script>
  import { storeCurrentPath } from "./../db/stores.js";
  const fs = require("fs");

  const electron = require("electron");
  const BrowserWindow = electron.remote.BrowserWindow;
  const dialog = electron.remote.dialog;

  const currentPath = $storeCurrentPath;
  console.log(`accessing assets: ${currentPath}`);

  let navCrumbs = currentPath.split("\\");
  let breadcrumbs = [];
  let lsCurrentPath;

  function upDirectory() {
    console.log(`navUp clicked, `, currentPath);
    console.log('navCrumbs ', navCrumbs);
    navCrumbs.pop()
    navCrumbs = navCrumbs
    let newPath = navCrumbs.join("\\")
    storeCurrentPath.set(newPath);
  }

  function selectFolder() {
    //renderer.js - a renderer process
    const { remote } = require("electron"),
      dialog = remote.dialog,
      WIN = remote.getCurrentWindow();

    let options = {
      title: "Select Folder",
      defaultPath: "C:\\Users\\Mike\\Desktop\\WEB DEV",
      buttonLabel: "Select Folder",
      filters: [],
      properties: ["openDirectory"]
    };

    //Synchronous
    let filePaths = dialog.showOpenDialog(WIN, options);
    console.log(filePaths);
    filePaths.then(res => {
      $storeCurrentPath = res.filePaths[0];
      currentPath = res.filePaths[0];
      console.log("currentPath: ", currentPath);
    });
  }

  function navigate(e) {
    breadcrumbs = [];
    console.log(e.target.textContent);
    console.log(breadcrumbs);
    lsCurrentPath = JSON.parse(localStorage.getItem("currentPath"));
    if (lsCurrentPath) {
      currentPath = lsCurrentPath;
      $storeCurrentPath = lsCurrentPath;
    } else {
      currentPath = currentPath;
      $storeCurrentPath = currentPath;
    }
    console.log("local currentPath: ", currentPath);
    console.log("global store currentPath: ", $storeCurrentPath);
    console.log("localStorage currentPath: ", lsCurrentPath);
  }
</script>

<style lang="scss">.nav-wrapper {
  display: flex; }

.nav {
  display: flex;
  align-items: center;
  justify-content: center; }

.breadcrumbs {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem; }

.breadcrumb {
  background: #225599aa;
  padding: 0.25rem 1rem;
  height: 3rem;
  display: flex;
  align-items: center; }
  .breadcrumb:after {
    content: "\\"; }
  .breadcrumb:hover {
    background: #22c5ffaa;
    color: #225599;
    cursor: pointer; }

.divider {
  font-weight: 900;
  color: black;
  font-size: 1rem; }

i {
  color: #22c5ff;
  font-size: 2rem;
  /* margin: 1rem; */
  /* padding: 0.25rem 1rem; */
  border-radius: 5px; }

.icon-container {
  width: auto;
  height: auto;
  padding: 0.25rem 1rem;
  margin: 0 0.5rem;
  background: #225599aa;
  display: flex;
  align-items: center;
  border-radius: 5px; }
  .icon-container:hover {
    background: #22c5ffaa;
    color: #225599; }

.up {
  font-size: 1rem;
  margin: 0.5rem 0.25rem;
  padding: 0 0.25rem; }

#upDirectory {
  background-image: url("../../assets/folder.png");
  background-size: 75%;
  background-repeat: no-repeat;
  background-position: center;
  width: 3rem;
  height: 3rem; }

#openDirectory {
  background-image: url("../../assets/022-development-2.png");
  background-size: 75%;
  background-repeat: no-repeat;
  background-position: center;
  width: 3rem;
  height: 3rem; }
</style>

<div class="nav-wrapper">
  <div class="nav">
    <div class="icon-container" on:click={selectFolder}>
      <i id="openDirectory" />
    </div>
  </div>
  <div class="nav">
    <div class="icon-container" on:click={upDirectory}>
      <i id="upDirectory" />
    </div>
  </div>
  <div class="breadcrumbs">
    {#each navCrumbs as crumb}
      <span class="breadcrumb" on:click={e => navigate(e)}>{crumb}</span>
      <!-- <span class="divider">></span> -->
    {/each}
  </div>

</div>
<h1>{currentPath}</h1>
